import { useState } from "react";
import * as S from "./PostModal.style";

export const PostModal = ({ setModalOpen, name }) => {
  const [title, setTitle] = useState("");
  const [content, setContent] = useState("");
  const [userNm, setUserNm] = useState("");
  const [password, setPassword] = useState("");
  const [updateState, setUpdateState] = useState(false);

  const onClose = () => {
    setModalOpen((prev) => !prev);
  };

  const onChangePost = (e) => {
    e.preventDefault();
    setUpdateState(true);
  };

  //   
  // TODO:       ** **
  if (updateState) {
    <S.ModalSection>
      <input
        type="text"
        value={name.title}
        placeholder=" "
        onChange={(e) => setTitle(...title, e.target.value)}
      />
      <textarea
        value={name.content}
        placeholder=" "
        onChange={(e) => setContent(...content, e.target.value)}
      />
      <input
        type="text"
        value={name.userNm}
        placeholder=" "
        onChange={(e) => setUserNm(...userNm, e.target.value)}
      />
      <input
        type="password"
        placeholder=" "
        value={name.password}
        onChange={(e) => setPassword(e.target.value)}
      />
    </S.ModalSection>;
    return;
  }

  return (
    <S.ModalSection>
      <button className="close" onClick={onClose}>
        <h2>X</h2>
      </button>
      <S.ModalTitle>{name.shelterNm}</S.ModalTitle>

      <S.ModalBody>
        <div className="titles"></div>
        <div className="texts">{name.title} </div>
        <div className="titles"></div>
        <div className="texts">{name.userNm}</div>
        <div className="titles"></div>
        <div className="textsC">{name.content} </div>
        {/* {updatePost} */}
        <div className="buttonWrapper">
          <button onClick={onChangePost} className="Pbutton">
            
          </button>
          {/* TODO:       */}
          <button className="Pbutton"></button>
        </div>
      </S.ModalBody>
    </S.ModalSection>
  );
};
