import logo from "../../asset/_logo.png";

export const Modal = ({ setModalOpen, mapName }) => {
  const onClose = () => {
    setModalOpen((prev) => !prev);
  };

  return (
    <ModalSection>
      <button className="close" onClick={onClose}>
        <h2>X</h2>
      </button>
      <ModalTitle>{mapName.markerParams.title}</ModalTitle>

      <img src={logo} alt="" />
      <ModalBody>
        {/*       */}
        <div>  : {mapName.markerParams.title} </div>
        <div>  : {mapName.markerParams.type}</div>
        <div> : {mapName.markerParams.address}</div>
        <div>   : {mapName.markerParams.qty}</div>
      </ModalBody>
    </ModalSection>
  );
};

